	module.exports = {"data":{"markdownRemark":{"html":"<h2>Nonfunctional Requirements</h2>\n<p>A nonfunctional requirement is one of two types of requirements you will recieve from a product owner. A nonfunctional requirement specifies the criteria that judges the operation of the system, instead of what the system does. They are normally distinguished from functional requirements due to their use of adverbs or descriptors.</p>\n<h3>Examples:</h3>\n<ul>\n<li>The printer should print 5 seconds after the button is pressed</li>\n<li>The code should be written in Java</li>\n<li>The UI should be easily navigable</li>\n</ul>\n<h4>More Information:</h4>\n<p><a href=\"http://reqtest.com/requirements-blog/functional-vs-non-functional-requirements/\">ReQtest</a> Explains the difference between functional and nonfunctional requirements</p>\n<p><a href=\"http://www.scaledagileframework.com/nonfunctional-requirements/\">Scaled Agile</a> Works through the process from finding to testing nonfunctional requirements</p>","fields":{"slug":"/agile/nonfunctional-requirements/"},"frontmatter":{"title":"Nonfunctional Requirements"}}},"pathContext":{"slug":"/agile/nonfunctional-requirements/"}}

